// Backend: 1) REACT_APP_BACKEND_URL si dfini (nettoy), 2) localhost en dev, 3) Railway en prod
const getBackendUrl = () => {
  // Utiliser la variable d'environnement en priorit
  if (process.env.REACT_APP_BACKEND_URL) {
    return process.env.REACT_APP_BACKEND_URL;
  }
  
  const hostname = window.location.hostname;
  if (hostname === "localhost" || hostname === "127.0.0.1") return "http://localhost:8001";
  // fallback pour les environnements non configurs
  return "https://vercel-api-fix.preview.emergentagent.com";
};

