// Fonction pour gnrer les couleurs des boutons bases sur le thme actuel
// TOUS LES BOUTONS ont la mme couleur base sur le thme slectionn
const getThemeButtonColors = (theme) => {
  // Couleur unique base sur le thme actuel
  const themeColor = {
    start: theme.primary,
    end: theme.secondary, 
    shadow: `rgba(${hexToRgb(theme.primary)}, 0.25)`
  };
  
  // Fonction helper pour convertir hex en rgb
  function hexToRgb(hex) {
    const r = parseInt(hex.slice(1, 3), 16);
    const g = parseInt(hex.slice(3, 5), 16);
    const b = parseInt(hex.slice(5, 7), 16);
    return `${r}, ${g}, ${b}`;
  }
  
  // Tous les boutons utilisent la mme couleur thmatique
  return {
    reset: themeColor,
    mystique: themeColor, 
    genese: themeColor,
    gemini: themeColor,
    versets: themeColor,
    generate: themeColor,
    concordance: themeColor
  };
}

function asString(x) {
  if (x === undefined || x === null) return "";
  if (typeof x === "string") return x;
  try { return JSON.stringify(x, null, 2); } catch { return String(x); }
}

function postProcessMarkdown(t) {
  const s = asString(t);
  return s
    .replace(/VERSET (\d+)/g, "**VERSET $1**")
    .replace(/TEXTE BIBLIQUE\s*:/g, "**TEXTE BIBLIQUE :**")
    .replace(/EXPLICATION THOLOGIQUE\s*:/g, "**EXPLICATION THOLOGIQUE :**")
    .replace(/Introduction au Chapitre/g, "**Introduction au Chapitre**")
    .replace(/Synthse Spirituelle/g, "**Synthse Spirituelle**")
    .replace(/Principe Hermneutique/g, "**Principe Hermneutique**");
}

